@import url(https://fonts.googleapis.com/css?family=Terminal+Dosis&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
:root {
    --bg: #7510F7;
    --bg-accent: #484a4d;
    --text-color: #dadce1;
    --nav-size: 60px;
    --item-size: 30px;
    --border: 1px solid #474a4d;
    --border-radius: 8px;
    --speed: 500ms;
}

.nav-icon {
    color: #fff;
    transition: color 500ms;
    transition: color var(--speed);
}

.nav-icon:hover {
    color: #aec6cf;
}

.nav-menu {
    background-color: #7510F7;
    background-color: var(--bg);
    width: 60px;
    width: var(--nav-size);
    height: 100%;
    display: flex;
    justify-content: center;
    position: fixed;
}

.nav-text {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 30px;
    font-size: var(--item-size);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-media-icon {
    display: flex;
    padding: 0px 0px 0px 17px;
    list-style: none;
    height: 40px;
}

.nav-media-icon a {
    text-decoration: none;
    font-size: 25px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    border-radius: 4px;
}

ul{
    width: 100%;
    /* its children will be flex-items */
    display: flex;
    /* move flex-items in column */
    flex-direction: column;
    justify-content: center;
}

ul > :nth-child(4){
    margin-top: auto;
}


:root {
    --font-family: Recoleta,Helvetica Neue,Helvetica,Arial,sans-serif;
    --font-size: 2rem
}

.profile{
    padding: 20px 0px 0px 60px;
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
}

.profile-content{
    font-family: Recoleta,Helvetica Neue,Helvetica,Arial,sans-serif;
    font-family: var(--font-family);
    font-size: 2rem;
    font-size: var(--font-size);
    width : 100%;
    height: 30vh;
    align-items: center;
    text-align: center;
}

.profile-name{

}

.profile-work {
    color: lightblue;
}

.profile-quota{

}

.profile-logo{
    width: 250px;
    height: 250px;
}

.profile-add-picture{
    width: 550px;
    height: 250px;
}


.container {
  width: 100%;
  height: 100vh;
  background-color: #232323;
  display: flex;
  justify-content: center;
  align-items: center; }
  .container .box {
    width: 300px;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start; }
    .container .box .title {
      width: 100%;
      position: relative;
      height: 50px; }
      .container .box .title .block {
        width: 0%;
        height: inherit;
        background: #ffb510;
        position: absolute;
        animation: mainBlock 2s cubic-bezier(0.74, 0.86, 0.4, 0.92) forwards; }
      .container .box .title h1 {
        font-family: 'Terminal Dosis';
        color: white;
        font-size: 32px;
        animation: mainFadeIn 2s forwards;
        animation-delay: 1.6s;
        opacity: 0; }
        .container .box .title h1 span {
          color: #ffb510;
          padding-left: 3px; }
    .container .box .role {
      width: 100%;
      position: relative;
      height: 30px;
      margin-top: -10px;
      display: flex;
      align-items: center; }
      .container .box .role .block {
        width: 0%;
        height: inherit;
        background: #e91e63;
        position: absolute;
        animation: secBlock 2s cubic-bezier(0.74, 0.86, 0.4, 0.92) forwards;
        animation-delay: 2s;
        position: absolute; }
      .container .box .role p {
        animation: secFadeIn 2s forwards;
        animation-delay: 3.2s;
        font-family: 'Terminal Dosis';
        font-weight: bold;
        color: white;
        font-size: 15px;
        letter-spacing: 3px;
        text-transform: uppercase;
        opacity: 0; }
        .container .box .role p span {
          color: #ffb510; }

@keyframes mainBlock {
  0% {
    width: 0%;
    left: 0px; }
  50% {
    width: 100%;
    left: 0px; }
  100% {
    width: 0;
    left: 100%; } }
@keyframes mainFadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
@keyframes secBlock {
  0% {
    width: 0%;
    left: 0px; }
  50% {
    width: 100%;
    left: 0px; }
  100% {
    width: 0;
    left: 100%; } }
@keyframes secFadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

